import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
//引入各个模块的 vuex仓库
import home from './home/home'
import login from './login/login'

export default new Vuex.Store({
  /**
   * 仓库 存储数据的地方
   * state: {},
   */
  
  /**
   * 可以理解为计算属性  用于简化仓库数据 让组件获取仓库的数据更加方便
   *  getters: {},
   */
 
  /**
   * 修改state的唯一手段 只能通过 mutations来修改seate
   *  mutations: {},
   */
  
  /**
   * 处理action  可以书写自己的业务逻辑 也可以处理异步
   * 这里可以处理业务逻辑 但是不能修改state仓库
   *  actions: { },
   */
  
  /**
   * 实现vueX仓库模块式开发储存  各自模块管理自己模块的vuex数据
   */

  modules: {
    home,
    login
  }
})
 