/**
 * 对于axios 进行二次封装
 */
import axios from "axios";

/**
 * 引入 nprogress 进度条样式插件
 */
import nprogress from 'nprogress';
import 'nprogress/nprogress.css';


/**
 * 利用axios对象的方法 create 创建一个axios实列
 * api 就是axios 稍微配置一下
 */
const api=axios.create({
    // 配置对象
    /**
     * baseURL  默认请求前缀  基础路径
     * timeout 代表请求超时时间  超出我们规定的时间就不再请求
     */
    baseURL:'http://localhost:3000',

    // baseURL:'http://127.0.0.1:5000',

    
    timeout:5000,
})

/**
 * 请求拦截器 在发请求之前  可以做一些事情
 * config 是配置对象 对象里面有个重要的东西 请求头
 */
api.interceptors.request.use((config)=>{
    
    nprogress.start()
    return config
})


/**
 * 响应拦截器 
 * 
 */
api.interceptors.response.use((res)=>{
    //成功的回调函数 ： 服务器响应数据回来后 响应拦截器拦截 做想做的事情
    nprogress.done()

    return res.data
},(error)=>{
    //失败的回调函数
    return Promise.reject(new Error('faile'))

})






/**
 * 对外暴露
 */

export default api;