import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '@/views/Home/index.vue'

/**
 * 先把VueRouter原型对象的push 备份一份
 */
let originPush=VueRouter.prototype.push;
let originReplace=VueRouter.prototype.replace;

/**
 * 重写VueRouter push|replace  防止多次调用编程式路由跳转报错  可有可无
 * @param {*} location  告诉原来的push方法 往哪儿跳转(传递哪些参数)
 * @param {*} resolve 
 * @param {*} reject 
 */
VueRouter.prototype.push=function(location,resolve,reject){
  if(resolve&&reject){
    //call||apply
    //相同点，都可以调用函数一次 都可以篡改函数上下文一次
    //不同点， call与apply传递参数；call传递参数可以用逗号隔开  ， apply方法执行，传递数组
    //使用备份的VueRouter原型对象
    originPush.call(this,location,resolve,reject)
  }else{
    originPush.call(this,location,()=>{},()=>{})
  }
}

VueRouter.prototype.replace=function(location,resolve,reject){
  if(resolve&&reject){
    originReplace.call(this,location,resolve,reject)
  }else{
    originReplace.call(this,location,()=>{},()=>{})
  }
}



Vue.use(VueRouter)

const routes = [

  /**
   * 页面重定向  当项目跑起来后  用户输入不属于路由里面的页面 立马定向到首页 或者 404 页面
   * 
   */
  {
    path:'*',
    redirect:'/'
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView,
    meta:{show:true},
  },
  {
    path: '/login',
    name: 'login',
    /**
     * 使用路由元信息meta属性  添加自定义判断条件  这样在我们的$route上 就会有meta这个属性 
     */
    meta:{show:false},
    component: () => import(/* webpackChunkName: "about" */ '@/views/Login/Login.vue'),
  },
  {
    path: '/register',
    name: 'register',
    meta:{show:false},
    component: () => import(/* webpackChunkName: "about" */ '@/views/Register/Register.vue'),
  },
  {
    /**
     * 这里的keyword 是路由传递params参数的占位
     */
    path: '/sarech/:keyword',
    name: 'sarech',
    meta:{show:true},
    component: () => import(/* webpackChunkName: "about" */ '@/views/Sarech/index.vue'),
  },
  {
    path: '/',
    name: 'echarts',
    meta:{show:false},
    component: () => import(/* webpackChunkName: "about" */ '@/views/Echarts/index.vue'),
  },
]

const router = new VueRouter({
  routes
})

export default router
