import axios from './api'


/**
 * get 传参 方法示例
 * export let $_checktoken = params => axios.get("/users/checktoken", {params})
 */

/**
 * post 示例
 * export let $_login = params => axios.post("/users/checkLogin", params)
 */


/**
 * 验证手机号是否已经注册
 */

export let $_checkingPhone = params => axios.get("/cellphone/existence/check", {params})

/**
 * 获取二维码kye 
 * /login/qr/key
 */
 export let $_loginQrKey = params => axios.get("/login/qr/key", {params})

/**
 * 获取登录二维码图片
 */
 export let $_loginQrCreate = params => axios.get("/login/qr/create", {params})


/**
 * 检测登录二维码是否过期
 */
 export let $_loginQrcheck = params => axios.get("/login/qr/check", {params})

/**
*用户邮箱密码登录接口 
*/

export let $_loginemail = params => axios.get("/login", {params})
